			position: relative;
			margin-bottom: 60px;

			z-index: 500;
			&::after,
			&::before {
				position: absolute;
				top: 0px;
				content: '';
				height: 100%;
				width: 30px;

				background-color: #fff;

				z-index: 500; }
			&::before {
				left: 0px; }
			&::after {
				right: 0px; } }
		.slick-list {
			position: relative;
			width: calc(100%); }
		.slick-slide {
			padding: 0 30px; }
		.slick-arrow {
			position: absolute;
			top: calc(125px - 27px);
			height: 35px;
			width: 35px;

			font-size: 0px;

			border: 1px solid #C7C7C7;

			background-color: transparent;
			background-repeat: no-repeat;
			background-position: center;
			background-size: 20px 14px;

			cursor: pointer;
			outline: none;
			transition: all .3s;
			z-index: 900;
			@media (hover) {//, (min-width:0\0)
				&:hover {
					top: calc(125px - 29px);
					height: 40px;
					width: 40px; } } }
		.slick-prev {
			left: -22px;

