				font-weight: 100;

				padding-top: 9px; }
			.details-btn {
				padding: 12px 36px;

				border-radius: 18px; }
			// @media (hover)
			// 	&:hover
			// 		.img-container
			// 			img
 }			// 				transform: scale(1.05)
		.all-discounts-btn {
