.gallery-section {
	.slick-track {
		display: flex; }
	.photo-slider {
		position: relative;
		width: 100%;

		overflow: hidden; }
	.item {
		display: flex;
		position: relative;

		min-height: 502px;
		max-height: 502px;
		img {
			height: 100%;
			width: 100%;

			object-fit: cover;
			object-position: center; }
		.desc {
			position: absolute;
			bottom: 0px;
			left: 0px;
			display: inline-flex;
			min-width: 200px;

			opacity: 0;

			transition: all .3s;
			.name {
				bottom: 0px;
				left: 0px;
				padding: 7px 10px 7px 27px;

				color: #fff;
				font-size: 12px;
				font-weight: 400;
				text-transform: uppercase;

				z-index: 100; }

			&::after {
				position: absolute;
				content: '';
				height: 100%;
				width: 100%;
				bottom: 0px;
				left: 0px;

				background-color: #24396E;
				mix-blend-mode: multiply; } }

		&::after {
			position: absolute;
			content: '';
			height: 100%;
			width: 100%;
			background-color: transparent;

			transition: all .3s; }
		&.slick-slide {
			height: auto;
			transition: all .3s;
			&::after {
				background-color: #24396E;
				mix-blend-mode: multiply; } }
		&.slick-active {
			width: 30vw!important; }
		&.slick-current + .slick-active {
			width: 40vw!important;
			.desc {
				opacity: 1; }
			&::after {
				background-color: unset; } } }

	.slick-arrow {
		position: absolute;
		height: 35px;
		width: 35px;
		top: calc(50% - 17px);

		font-size: 0px;

		border: 1px solid #426ad2;

		background-color: transparent;
		background-position: center;
		background-repeat: no-repeat;
		background-size: 19px 14px;

		outline: none;
		z-index: 500;
		@media (hover) {//, (min-width:0\0)
			&:hover {
				height: 39px;
				width: 39px;
				top: calc(50% - 19px); } } }
	.slick-prev {
		left: 20%;
		background-image: url(../img/arrow-left-blue.svg);
		@media (hover) {//, (min-width:0\0)
			&:hover {
				left: calc(20% - 2px); } } }
	.slick-next {
		right: 20%;
		background-image: url(../img/arrow-left-blue.svg);
		transform: rotate(180deg);
		@media (hover) {//, (min-width:0\0)
			&:hover {
				right: calc(20% - 2px); } } } }

